html, body, * {
  margin: 0; padding: 0;
}

.App {
  text-align: center;
}

.logo {
  width: 100vw;
  padding: 22px;
  font-size: 36px;
  background: skyblue;
  letter-spacing: 2px;
}

.title {
  font-size: 48px;
  margin: 24px;
}

.battle {
  display: flex;
  justify-content: center;
}

.battle-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.battle-loading {
  padding: 96px;
}

.service {
  display: flex;
  flex-direction: column;
  padding: 48px;
  width: 320px;
}

.service-image {
  width: 100%;
}

.service-image img {
  width: 192px;
  /*max-width: 400px;*/
}

.service-text {
  font-size: 24px;
  padding: 32px 12px;
  font-weight: bold;
}

.btn {
  border: 0;
  background: none;
}

.btn-primary {
  width: 100%;
  border: 4px inset red;
  font-size: 18px;
  transition: .3s all;
  height: 42px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.btn-primary div {
  transition: .3s all;
}

.btn-primary:hover > div {
  transform: scale(2);
}

.learn-more {
  display: inline-block;
  font-size: 16px;
  padding: 12px;
  color: black;
  text-decoration: none;
}

.learn-more:hover {
  text-decoration: underline;
}